import React from "react"
import styled from "styled-components"
const Footer = () => {
  return (
    <Styled>
      <div className="flex">
        <p>Powered by:</p>
      </div>
    </Styled>
  )
}
const Styled = styled.div`
  width: 100%;
  height: 8vh;
  background: linear-gradient(180deg, white, #e6e6e6);
  border: 1px solid #e6e6e6;
  align-content: center;
  div {
    display: flexbox;
  }
`
export default Footer
